
.mainbody-outer{
      background: #f5f5f5;
      overflow: hidden;
      /*border:1px solid red;*/
}
.main_outer .main_header_nav ul a{
     float:left;
     /*border:1px solid red;*/
     width:139px;
     height:53px;
     padding-top:25px;
}
.main_outer .main_header_nav ul a .cli{
    color:#fff;
    background:url(../assets/images/icon_tie2.png) no-repeat;
    transform:translateY(7px);
    transition:transform 0.7s;
}
.main_outer .main_header_nav ul a div{
        width:139px;
        height:43px;
        line-height: 43px;
        /*border:1px solid red;*/
        text-align: center;
        font-size: 17px;
        color:#696969;
        cursor: pointer;
        margin-top:5px;
        background: url(../assets/images/icon_tie1.png) no-repeat;
}






