
.order_manage[data-v-aa3024f6]{
  height:100%;
  overflow: hidden;
  /*border:1px solid red;*/
}
.order_head[data-v-aa3024f6]{
  width:100%;
  height:70px;
  /*border:1px solid #ccc;*/
  /*padding:0px 3%;*/
}
/*tab切换*/
.order_head .order_tab[data-v-aa3024f6]{
  float:left;
  padding-top:20px;
  /*border:1px solid red;*/
}
ul li[data-v-aa3024f6]{float:left;
}
.order_head .order_tab ul li[data-v-aa3024f6]{
  cursor: pointer;
  height:38px;
  line-height: 38px;
  text-align: center;
  padding:0px 30px;
  border-bottom:1px solid #ccc;
  font-size: 15px;
  color:#959595;
}
.order_head .order_tab .selected[data-v-aa3024f6]{
  color:#ff7b3f;
  border:1px solid #ccc;
  background: #f5f5f5;
  border-bottom: 0px;
}
/*搜索栏*/
.order_head .search_parms[data-v-aa3024f6]{
  float:right;
  padding-top:25px;
  /*border:1px solid red;*/
}
.order_head .search_parms ul li[data-v-aa3024f6]:first-child{
  margin-right:40px;
}
.order_head .search_parms ul li span[data-v-aa3024f6]{color:#696969;font-size:16px;margin-right:5px;
}
.order_head .search_parms ul li select[data-v-aa3024f6]{width:115px;color:#959595;height:30px;line-height: 30px;text-indent: 5px;background: #f5f5f5;
}
.order_head .search_parms ul input[type="text"][data-v-aa3024f6]{height:26px;line-height: 26px;width:270px;margin-right:10px;text-indent: 5px;
}
.order_head .search_parms ul input[type="button"][data-v-aa3024f6]{width:63px;height:28px;line-height: 28px;background: #f5f5f5;border:1px solid #959595;
}
.order_data[data-v-aa3024f6]{
  width:100%;
  height:200px;
  /*border:1px solid #ccc;*/
  /*padding:0 2%;*/
  margin-top:15px;
}
.order_data ul[data-v-aa3024f6]{width:100%;
}
.order_data .ul1 li[data-v-aa3024f6]{width:16%;background:#f5f5f5;height: 50px;line-height: 50px;text-align: center;color:#696969;
}
.ul2 li[data-v-aa3024f6]{width:16%;font-size:14px;color:#959595;text-align: center;height:60px;line-height: 60px;border-bottom:1px solid #959595;
}
.ul3 li[data-v-aa3024f6]{width:35px;height:35px;line-height:35px;text-align: center;color:#696969;background: #f5f5f5;margin-left:5px;margin-top:20px;cursor: pointer;
}
.ul3 li[data-v-aa3024f6]:last-child{width:100px;background: #ff7b3f;color:#fff;
}



